<script>
import db from "@/assets/cardlist/[S7R] 拡張パック 蒼空ストリーム.json";

const cardlist = [
  {
    name: "ユキメノコ",
    rarity: "UR",
    ver: "s07r",
    gid: "31",
    cid: "10087",
    id: "087/067",
    price: "3480",
    // img_src: "@/assets/cardlist/card/[s07r].10087-ユキメノコ.jpg",
    img_src: require("@/assets/cardlist/card/[s07r].10087-ユキメノコ.jpg"),
    stock: 0,
    count: 0,
  },
  {
    name: "嵐の山脈",
    rarity: "UR",
    ver: "s07r",
    gid: "31",
    cid: "10089",
    id: "089/067",
    price: "2980",
    // img_src: "@/assets/cardlist/card/[s07r].10089-嵐の山脈.jpg",
    img_src: require("@/assets/cardlist/card/[s07r].10089-嵐の山脈.jpg"),
    stock: 3,
    count: 1,
  },
  {
    name: "レックウザVMAX",
    rarity: "HR",
    ver: "s07r",
    gid: "31",
    cid: "10082",
    id: "082/067",
    price: "9980",
    // img_src: "@/assets/cardlist/card/[s07r].10089-嵐の山脈.jpg",
    img_src: require("@/assets/cardlist/card/[s07r].10082-レックウザVMAX.jpg"),
    stock: 1,
    count: 1,
  },
  {
    name: "レックウザVMAX",
    rarity: "HR",
    ver: "s07r",
    gid: "31",
    cid: "10083",
    id: "083/067",
    price: "79800",
    // img_src: "@/assets/cardlist/card/[s07r].10089-嵐の山脈.jpg",
    img_src: require("@/assets/cardlist/card/[s07r].10083-レックウザVMAX.jpg"),
    stock: 1,
    count: 1,
  },
];

export default {
  data() {
    return {
      count: 1,
      btn_cart: require("@/assets/btn_cart.png"),
      btn_sold_out: require("@/assets/btn_sold_out.png"),
      cl: db,
      Rarity: ["UR", "HR", "SR", "RRR", "RR", "R", "U", "C"],
      // cl: cardlist,

      // idCart:cl.map(function(item){
      //   return item.id
      // })
      // cl2:cardlist2
    };
  },
  computed: {
    computedURL(url) {
      return require(url);
    },
  },
  methods: {
    clr(rarity) {
      let result = this.cl.filter(function (item) {
        if (item.Rarity == rarity) {
          return item;
        }
      });
      return result;
    },
    countHandler(rarity,index, count) {
      // itemcount=this.$store.state.cardlist[index].count
      // let data=this.$store.state.cardlist[index]
      // // if(data.count+count<1||data.count+count>10) return
      // data.count+=count
      // this.$store.commit('UPDATE_LIST',{index,data})
      
      this.clr(rarity)[index].count += count;
      console.log(this.clr(rarity)[index].count);
    },
    isDisable(rarity,index, count) {
      // if (this.cl[index].count==0) {
      //   return true
      //   }
      // else{
      //   return this.cl[index].count==count
      //   }

      // console.log( "isdisale " +this.cl[index].count == 0 ? true : this.cl[index].count == count);

      return this.clr(rarity)[index].stock == 0 ? true : this.clr(rarity)[index].count == count;
    },
    addToCart(rarity,index) {
      let card = this.clr(rarity)[index];
      let data = {
        Name: card.Name,
        Rarity: card.Rarity,
        Ver: card.Ver,
        Gid: card.Gid,
        Cid: card.Cid,
        ID: card.ID,
        Price: card.Price,
        Img_src: card.Img_src,
        stock: card.stock,
        count: card.count,
      };
      // console.log(data);
      let result = this.$store.state.cart
        .map(function (item) {
          return item.ID;
        })
        .indexOf(data.ID);
      let resultItem = this.$store.state.cart[result];

      // console.log(result);
      // console.log(data);

      //是否購物車有此物件
      if (result != -1) {
        console.log("data.count " + data.count);

        data.count =
          resultItem.count + data.count > resultItem.stock
            ? resultItem.stock
            : resultItem.count + data.count;
        console.log(
          "this.$store.state.cart[" +
            result +
            "].count " +
            this.$store.state.cart[result].count
        );
        console.log("data.count " + data.count);

        card.stock = (card.stock - card.count >0)?card.stock - card.count:0;
        // if (card.stock == 0) card.count = 0;
        card.count=(card.stock>0)?1:0;

        this.$store.commit("UPDATE_CART", [result, data]);
      } else {
        // data.count=this.$store.state.cart[result].count+data.count
        this.clr(rarity)[index].stock = this.clr(rarity)[index].stock - this.clr(rarity)[index].count;
        this.$store.commit("ADD_CART", data);
      }
      // result?this.$store.commit('UPDATE_CART', {index,data}):this.$store.commit('ADD_CART', data);
    },
    test() {
      console.log("test");
      console.log(cardlist);
      console.log(db);
    },
  },
};
</script>

<template src='./template.html'></template>
<style src='@/assets/css/reset.css' scoped></style>
