<script>
export default {
  data() {
    return {
      imgurl:'@/assets/img',
      // testurl:'.../.../assets/img/[s04].10104-ピカチュウV.jpg',
      // testurl:'@/assets/img/test.jpg',
      // testurl:require('@/assets/img/test.jpg'),
      testurl:'/img/test.jpg',


      cart: [
        {
          name: "ピカチュウVMAX",
          url: require('@/assets/img/[s04].10031-ピカチュウVMAX.jpg'),
          // url: '[s04].10031-ピカチュウVMAX.jpg',
          
          price: 1000,
          count: 2,
          id: "10104",
        },
        {
          name: "名探偵ピカチュウ",
          url:require('@/assets/img/[spromo-100].10099-名探偵ピカチュウ.jpg'),
          // url:'[spromo-100].10099-名探偵ピカチュウ.jpg',

          price: 1000,
          count: 1,
          id: "10031",
        },
      ],
    };
  },
  computed:{
    total(){
      let sum=0;
      for(let index in this.$store.state.cart){
        sum+=this.$store.state.cart[index].Price*this.$store.state.cart[index].count;
        // console.log(item);
      }
      console.log(sum);
      return sum ;
    },
  },
  // props:{
  //     cart:[]
  // },
  methods:{
      countHandler(index,count){
        let data=this.$store.state.cart[index]
        // if(data.count+count<1||data.count+count>10) return 
        data.count+=count
        this.$store.commit('UPDATE_CART',[index,data])

      },
      deleteItem(index){
        this.$store.commit('REMOVE_CART',index)
      },
      isDisable(index,count){
        return this.$store.state.cart[index].count==count
        // return this.$store.state.cart[index].count == 0 ? true : this.$store.state.cart[index].count == count;

      }
  }
};
</script>
